import React, { useContext, useEffect, useState } from 'react';
import './Home.css';
import { DataContext, SelectedContext } from '../select';
import { NavLink } from 'react-router-dom';

export default function Home() {
    const [tests, setTests] = useState([]);
    const [select, setSelect] = useContext(SelectedContext)
    const [data, setData] = useContext(DataContext);
    function handleClick(e, id) {
        setSelect(e);
        setData(prev => {
            let newArr = prev.map(elem => {
                if (elem.id === e.id) {
                    return { ...elem, isSaw: true }
                } else {
                    return elem;
                }
            })
            return newArr
            // return [...prev, data[id].isSaw = true]
        })
            ;
    }

    return (
        <div>
            <div className="center mt-5">
                <h1 className="text-center pt-2">Raqamni tanlang</h1>
                <div className="container pb-2 text-center">
                    {
                        data.map((elem, id) => (
                            // #e67e22#ebcfc4
                            <NavLink to="/detail"><button key={id} style={elem.isSaw ? { backgroundColor: "#a9daeb" } : { backgroundColor: "#3993cf" }} onClick={() => { handleClick(elem, id) }} className="bttn-pill bttn-md  button mt-2">{elem.id}</button></NavLink>
                        ))
                    }
                </div>
            </div>
        </div>
    )
}
