import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Home from './pages/Home';
import First from './pages/First';
import { DataProvider, SelectProvider } from './select';
import Detail from './pages/Detail';

function App() {
  return (
    <SelectProvider>
      <DataProvider>
        <Router>
          <Switch>
            <Route exact path="/">
              <First />
            </Route>
            <Route exact path="/home">
              <Home />
            </Route>
            <Route exact path="/result">
              <Home />
            </Route>
            <Route exact path="/detail">
              <Detail />
            </Route>
          </Switch>
        </Router>
      </DataProvider>
    </SelectProvider>
  );
}

export default App;
