import React, { useContext, useEffect, useState } from 'react'
import { SelectedContext } from '../select';
import './Detail.css';

export default function Detail() {
    const [select, setSelect] = useContext(SelectedContext)
    const [isSelected, setIsSelected] = useState(false)
    const [isTrue, setIsTrue] = useState(false)
    function handleClick(elem) {
        let id = select.variants.indexOf(elem);
        if (select.rightVariant === id) {
            setIsTrue(true)
        } else {
            setIsTrue(false)
        }
        setIsSelected(true);
    }
    function Result() {
        return (
            <div className="wrap">
                <center>
                    <img className="gif" src={isTrue ? "well.gif" : "wrong.webp"} alt="natija" />
                    <h1 className="mt-3 res-title">
                        {
                            isTrue ? "Tabriklaymiz javob to'g'ri"
                                : "Javob noto'g'ri qaytadan urinib ko'ring"
                        }
                    </h1>
                </center>
            </div>
        )
    }
    function Test() {
        return (
            <div className="wrap">

                <div className="questions_content ">
                    {/* <p id="number_1">5</p>
                <p id="operation">*</p>
                <p id="number_2">10</p> */}
                    <h2 className="">{select.question}</h2>
                </div>

                {/* <!-- answers content --> */}
                <div className="answers_content">
                    <div className="row pt-1">
                        {
                            select.variants.map(elem => (
                                <div className="answer_box" onClick={() => { handleClick(elem) }}>
                                    <div className="answer_btn">A</div>
                                    <span className="answer_text">{elem}</span> <br />
                                </div>
                            ))
                        }

                    </div>
                    {/* <div className="row">
                    <div className="answer_box">
                        <div className="answer_btn">C</div>
                        <span className="answer_text">36</span>
                    </div>
                    <div className="answer_box">
                        <div className="answer_btn">D</div>
                        <span className="answer_text">38</span>
                    </div>
                </div> */}
                </div>
            </div>
        )
    }
    return (
        <>
            {
                isSelected ? <Result /> : <Test />
            }
        </>
    )
}
